@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  min-width: 100%;
  background-color: black;
  color: white;
  font-family: "Roboto Condensed", sans-serif;
}

.root {
  width: 100%;
}

.marg {
  margin-top: 3rem;
  margin-left: 3rem;
  margin-right: 3rem;
}
.home_marg {
  margin-bottom: 5rem;
}
span:hover {
  color: rgb(98, 235, 228);
}
.icon_st {
  margin: 0rem 4.5rem 4rem 0rem;
  font-size: 4.5rem;
}

@media only screen and (max-width: 1000px) {
  .icon_st {
    margin: 0rem 4rem 3.5rem 0rem;
    font-size: 3.5rem;
  }
}
@media only screen and (max-width: 850px) {
  .icon_st {
    margin: 0rem 3.5rem 3rem 0rem;
    font-size: 3.25rem;
  }
}
@media only screen and (max-width: 770px) {
  .icon_st {
    font-size: 3rem;
    margin: 0rem 3rem 2.5rem 0rem;
  }
}
@media only screen and (max-width: 690px) {
  .icon_st {
    font-size: 3rem;
    margin: 0rem 2rem 2.5rem 0rem;
  }
}
@media only screen and (max-width: 592px) {
  .icon_st {
    font-size: 3rem;
    margin: 0rem 1.5rem 2rem 0rem;
  }
}
@media only screen and (max-width: 415px) {
  .icon_st {
    font-size: 3rem;
    margin: 0rem 2rem 2.5rem 0rem;
  }
}

